<template>
  <div>
    <!-- 添加角色按钮 -->
    <el-button type="primary" class="add" @click="add" >添加</el-button>
    <!-- 添加角色组件 -->
    <v-add :info="info" ref="add"></v-add>
    <!-- 角色列表组件 -->
    <v-list @edit="edit"></v-list>
  </div>
</template>

<script>
import vAdd from './components/add.vue';
import vList from './components/list.vue';
export default {
  data(){
    return {
      info:{
        show:false,
        title:'添加角色',
        isAdd:true
      }
    }
  },
  components:{
    vAdd,
    vList,
  },
  methods:{
    // 添加角色
    add(){
      this.info.show = true;
      this.info.title = '添加角色';
      this.info.isAdd = true;
    },
    edit(id){
      // 触发编辑按钮，将显示添加的子组件，并将id传递过去
      this.info.show = true;
      this.info.title = '编辑角色';
      this.info.isAdd = false;
      // 根据ID获取修改的数据,获取add子组件中的getDetail方法
      this.$refs.add.getDetail(id);
    }
  }

}
</script>

<style>

</style>



