<template>
   <div>
    <el-dialog :title="info.title" :visible.sync="info.show">
  <el-form :model="form">
    <el-form-item label="角色名称"  label-width="120px">
      <el-input v-model="form.rolename" autocomplete="off"></el-input>
    </el-form-item>
    <el-form-item label="角色权限"  label-width="120px">
      <el-tree
            :data="menuList"
            show-checkbox
            node-key="id"
            :default-checked-keys="defaultKey"
            :props="defaultProps"
            ref="tree"
          ></el-tree>
    </el-form-item>
    <el-form-item label="菜单状态"  label-width="120px">
      <el-switch  v-model="form.status" active-color="blue" inactive-color="grey" :active-value="1"
    :inactive-value="2">
</el-switch>
    </el-form-item>
  </el-form>
  <div slot="footer" class="dialog-footer">
    <el-button @click="cancel">取 消</el-button>
    <el-button type="primary" @click="add"  v-if="info.isAdd==true">添加</el-button>
    <el-button type="primary" @click="update" v-else>修改</el-button>
  </div>
</el-dialog>

  </div>
</template>

<script>
import {mapGetters,mapActions} from 'vuex';
import {requestRoleAdd,requestRoleOne,requestRoleUpdate} from '../../../util/request';
import { successAlert, warningAlert } from '../../../util/alert';
export default {
  props:['info'],
  data(){
      return {
        form: {
          rolename: '',
          menus:'',
          status:1,
        },
        value:true,
        data:[],
        defaultKey:[],//树形结构中默认为空
        defaultProps: {
          children: 'children',
          label: 'title'
        }
      }
    },
    computed:{
      ...mapGetters({
        menuList:'menu/list',
      })
    },
    methods:{
      ...mapActions({
        // 获取菜单列表
        requestMenuList : 'menu/menuList',
        requestRoleList:"role/roleList",
      }),
      // 取消添加角色
      cancel(){
        this.info.show = false;
      },
      // 添加角色
      add(){
        // 获取tree的可以并赋值给this.menus
        this.form.menus = JSON.stringify(this.$refs.tree.getCheckedKeys());
        // 以post方式发送请求
        requestRoleAdd(this.form).then(res=>{
          if(res.data.code == 200){
            successAlert('添加成功');
            this.cancel();
            this.empty();
            this.requestRoleList();
          }else{
            warningAlert('添加失败');
          }
        })
      },
      empty(){
        // 重置添加数据
        this.form = {
           rolename: '',
          menus:'',
          status:1,
        };
        // 将树形结构的数据，选中的key置空
        this.$refs.tree.setCheckedKeys([]);
      },
      getDetail(id){
        // 获取修改的数据
        requestRoleOne({id:id}).then(res=>{
          // 获取数据成功
          if(res.data.code == 200){
            successAlert('修改成功');
            this.form = res.data.list;
            // 把ID存起来
            this.form.id = id;
            // 将树形结构的默认值从数据库中查询出来
            this.defaultKey = JSON.parse(res.data.list.menus)

          }else{
                warningAlert('删除失败');
              }
        })
      },
      update(){
        // 获取树的可以并赋值给form.menus
        this.form.menus = JSON.stringify(this.$refs.tree.getCheckedKeys());
        requestRoleUpdate(this.form).then(res=>{
          if(res.data.code == 200){
            successAlert('修改成功');
            this.cancel();
            this.empty();
            this.requestRoleList();
          }else{
                warningAlert('删除失败');
              }
        })
      }
    },

    mounted(){
      if(this.menuList.length == 0){
          this.requestMenuList();
        }
    }

}
</script>

<style>

</style>



